import { useHistory } from "react-router";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { logout, setLoggedInUser } from "../../store/actions/userActions";
import { AccountMenu } from "../AccountMenu";
import NiceButton from "../NiceButton/NiceButton";
import homeLogo from "../../assets/icons/home.png";

export const AppHeader = () => {
  const loggedUser = useSelector((state) => state.userReducer.user);

  const history = useHistory();
  const dispatch = useDispatch();

  const onGoHome = () => history.push("/");

  const onLogout = () => {
    dispatch(logout()).then(() => {
      history.push("/");
    });
  };

  const IconHome = () => <img src={homeLogo} alt="" />;

  useEffect(() => {
    if (!loggedUser) dispatch(setLoggedInUser());
  }, [dispatch, loggedUser]);

  return (
    <header className="app-header full">
      <NiceButton Icon={IconHome} onClick={onGoHome} className="home-logo" />
      <AccountMenu
        fullname={loggedUser?.fullname}
        balance={loggedUser?.coins}
        logout={onLogout}
        history={history}
      />
    </header>
  );
};
