import { httpService } from "./httpService";

export const transferService = {
  addTransfer,
  getTransfers,
  getTransfersByContactId,
};

async function addTransfer(contactEmail, transferAmount, loggedUserEmail) {
  return await httpService.post(`transfer/`, {
    contactEmail,
    transferAmount,
    loggedUserEmail,
  });
}

async function getTransfers() {
  try {
    return await httpService.get(`transfer/`);
  } catch {}
}

async function getTransfersByContactId(userId) {
  try {
    return await httpService.get(`transfer/${userId}`);
  } catch {}
}
